


article {
    width: 60%;
    margin: auto;
  }
  .shopping-cart-header{
    font-family: 'Playfair Display', serif;
    text-align: center;
    margin-top: 40px;
    font-size: 2rem;
    color: green;
  }
  .shopping-cart-header-hide{
    font-family: 'Playfair Display', serif;
    text-align: center;
    margin-top: 40px;
    font-size: 2rem;
    color: green;
  }
 .cart-title{
  font-family: 'Cormorant Garamond', serif;
  display: flex;
  font-size: 1rem;
  justify-content: space-between;
  margin-top: 50px;
  padding: 10px;
  border-bottom: 2px solid var(--grey-color);

 }
 .cart-title-quantity{
  padding-left: 0px;
 }
 .cart-title-price{
  padding-left: 270px;
 }
  .cart_box img {
    width: 90px;
    height: 100px;
    border: 1px solid var(--grey-color);
    padding: 10px 1px;
    border-radius: 7px;
  }
  .cart_img {
    display: flex;
    width: 400px;
  
  }
  
  .cart_img p {
    
    margin-left: 10px;
    margin-bottom: 10px;
  }
  .cart-product-name{
    font-weight: bold;
  }
  .cart_img .img-desc{
    display: block;

  }
  .cart-product-price{
    font-weight: bold;
  }
  .cart_box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    border-bottom: 1px solid var(--grey-color);
    padding-bottom: 10px;
  }
  .cart-quantity{
    border: 1px solid var(--gainsboro-color);
    width: 100px;
    text-align: center;
    padding: 7px 1px;
    border-radius: 5px;
    background-color: #f2f2f2;
    margin-top: 5px;
    transition: all .7s ease;
  }

.cart-quantity:hover{
background-color: #d3d3d3;
}
  .cart-quantity span{
    font-size: 1.2rem;
    margin: 0px 5px;
    font-weight: 700;
    cursor: pointer;
  }
  
  .cart_box div:nth-child(2) button {
    padding: 5px 10px;
    font-weight: bold;
    margin-right: 5px;
  }
  
  .total-price button{
    padding: 8px 15px;
    font-weight: bold;
    font-size: .5rem;
    letter-spacing: 2px;
    border-radius: 5px;
    border: none;
    background-color: var(--gainsboro-color);
    color: var(--black-color);
    transition: all .7s ease;
  }
  .total-price button:hover{
    background-color: #cc0000;
    color: green;
  }
  
  .total-price span{
    color: blue;
    /* color: var(--white-color); */

    display: inline-block;
    width: 80px;
    padding: 10px 10px;
    border-radius: 5px;
    margin-right: 10px;
    font-weight: bold;
  }
  .sub-total {
    /* display: flex;
    justify-content: space-between; */
    margin-left: 77%;
  }
  
  .sub-total-title{
    font-size: 1.6rem;
    color: var(--main-color);
    font-weight: bold;
    letter-spacing: 2px;
    font-family: 'Uchen', serif;
  }
  .sub-total-price{
    font-size: 1.5rem;
    /* background-color: var(--lime1-color); */
    color: green;
    padding: 5px 15px;
    font-weight: bold;
    border-radius: 8px;
  }
  .clear-cart-button{
    border: none;
    width: 170px;
    text-align: center;
    padding: 10px 5px;
    border-radius: 5px;
    background-color: #cc0000;
    margin-top: -50px;
    color: var(--white-color);
    font-size: 1.3rem;
    transition: all 1s ease;
   
  }
  .clear-cart-button:hover{
    background-color: #ff3333;
    color: #acacac;
  }

  @media only screen and (max-width: 1150px) {
    article {
      width: 90%;
      margin: auto;
    }

    .cart-title{
      
      
      
      width: 83%;
