.contact-container{
    min-height: 90vh;
    /* background: url(https://images.unsplash.com/photo-1423666639041-f56000c27a9a?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1474&q=80) no-repeat; */
    background: linear-gradient(rgba(255,255,255,.5), rgba(255, 255, 255, 0.9)), url(https://store.storeimages.cdn-apple.com/4668/as-images.apple.com/is/iphone-13-model-unselect-gallery-1-202207?wid=5120&hei=2880&fmt=p-jpg&qlt=95&.v=1654893619853) no-repeat ;
    /* background: url(../assets/istockphoto-480743480-612x612.jpg); */
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
     margin-top: -160px;
     margin-bottom: -200px;
    width: 100%;
    
  }

.contact-section{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .contact-info{
    color: #000;
    max-width: 400px;
    line-height: 65px;
    padding-left: 50px;
    font-size: 18px;
    
  }
  
  .contact-info i{
    margin-right: 20px;
    font-size: 25px;
  }
  
  .contact-form{
    max-width: 700px;
    margin-right: 50px;
  }
  
  .contact-info, .contact-form{
    flex: 1;
  }
  
  .contact-form-title{
    color: #fff;
    text-align: center;
    font-size: 4em;
    text-transform: uppercase;
    margin-bottom: 30px;
  }


  .contact-form-message {
    color: #fff;
    text-align: center;
    font-size: 3em;
  }
  
  .contact-form .text-box{
    /* background: rgb(87, 87, 87); */
    background-color: #ddd;
    color: black;
    border: none;
    width: calc(50% - 10px);
    height: 50px;
    padding: 12px;
    font-size: 15px;
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    opacity: 0.9;
  }
  
  .contact-form .text-box:first-child{
    margin-right: 15px;
  }

  .text-box::placeholder,  .contact-form textarea::placeholder {
    color: rgb(126, 126, 126);
    font-size: 15px;
    font-weight: bolder;

  }


  .contact-form textarea{
    /* background: rgb(87, 87, 87); */
    background-color: #ddd;
    color: #000;
    border: none;
    width: 100%;
    padding: 12px;
    font-size: 15px;
    min-height: 200px;
    max-height: 400px;
    resize: vertical;
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    opacity: 0.9;
  }
  
  .contact-button {
    font-size: 1rem;
    background-color: #000;
    padding: .8em 3rem;
    color: #fff;
    cursor: pointer;
    border-radius: 7px;
    transition: all .7s ease;

}

.contact-button a {
    text-decoration: none;
    color: #fff;
    transition: all .7s ease;

}



.contact-button:hover {
  color:#7c7c7c;
}
.contact-message {
  display: flex;
  height: 30vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.contact-button:hover{
   
    color:rgb(117, 117, 117);
}
  
  @media screen and (max-width: 950px){
    .contact-section{
      flex-direction: column;
    }
  
    .contact-info, .contact-form{
      margin: 30px 50px;
    }
  
    .contact-form-title{
      font-size: 34px;
    }
  
    .contact-form .text-box{
      width: 100%;
    }

  }
    

