   border: 1px solid #8c8c8c;
  }
  .productImg-admin{
    
    flex: 5%;
    max-width: 100px;
    height: 80px;
    margin: auto;
  }
  .productName-admin{
    flex: 5%;
    font-weight: bold;
    font-family: 'Uchen', serif;
    font-size: 1.1rem;
    
  }
  .productDescription-admin{
    flex: 32%;
  }
  
  .productPrice-admin{
    flex: 3%;
    color: #00802b;
    font-weight: 700;
  }
  .addtocart-admin{
    flex: 3%;
  }
  .delete-product-admin{
    flex: 3%;
  }
  
  .trash-admin{
    color: black;
    font-size: 1.3rem;
    border-radius: 5px;
    background-color: #ddd;
    height: 40px;
    width: 40px;
    padding: 10px;
    transition: all .7s ease;
  }
  
  .trash-admin:hover{
    color: #e60000;
    background-color: #ccc;
  } 
  
  
  
  .edit-product{
    color: black;
    font-size: 1.3rem;
    border-radius: 5px;
